<template>
  <div>
    <div class="app-top">
      <div class="add">
        <el-button v-waves size="small" type="primary" @click="add">添加幻灯片</el-button>
      </div>
      <div class="filter">
        <div class="filter-item">
          <el-input v-model="name"></el-input>
        </div>
        <el-button v-waves class="filter-item" type="primary" icon="el-icon-search" @click="init">搜索</el-button>
      </div>
    </div>
    <el-table v-loading="listLoading"
              :data="list"
              border
              fit
              highlight-current-row
              style="width: 100%;">
      <el-table-column label="序号" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.orderId }}</span>
        </template>
      </el-table-column>
      <el-table-column label="图片" align="center">
        <template slot-scope="scope">
          <img :src="scope.row.imgUrl" height="50">
        </template>
      </el-table-column>
      <el-table-column label="标题" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <!--<el-table-column label="状态" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.state === '1' ? '有效' : '无效' }}</span>
        </template>
      </el-table-column>-->
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <span style="color: #E6A23C;margin-right: 10px;display: inline-block" @click="edit(scope.row)">
            <svg-icon icon-class="edit" />
          </span>
          <span style="color: red" @click="delete1(scope.row)">
            <svg-icon icon-class="status" />
          </span>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="70px" class="demo-ruleForm">
        <el-form-item label="名称" prop="name">
          <el-input v-model="ruleForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="序号" prop="orderId">
          <el-input v-model="ruleForm.orderId" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="ruleForm.remark" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="展示图" prop="imgUrl">
          <el-upload
            class="upload-demo"
            ref="upload"
            :action="upLoadUrl"
            :with-credentials=true
            :on-success="successUpload"
            :before-upload="beforeUpload"
            :before-remove="movefile"
            :on-error="errorUpload"
            :file-list="fileList"
            list-type="picture"
            :limit="1"
            :on-exceed="morefile">
            <el-button size="small" class="uploadbtn" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">
              推荐宽高: 1024x768 图片格式: jpg,png,jpeg, 小于5MB
            </div>
          </el-upload>
        </el-form-item>
<!--        <el-form-item label="是否有效" prop="state">-->
<!--          <el-select v-model="ruleForm.state">-->
<!--            <el-option v-for="item in states" :key="item.value" :label="item.label" :value="item.value"/>-->
<!--          </el-select>-->
<!--        </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureForm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/usr'
import waves from '@/directive/waves' // 水波纹指令
import config from '@/utils/config'
export default {
  directives: {
    waves
  },
  name: 'banner',
  data () {
    const checkOrder = (rule, value, callback) => {
      if (value) {
        value = value.trim()
        var reg = /^[1-9]\d*$/
        if (reg.test(value)) {
          callback()
        } else {
          callback(new Error('序号需填写正整数！'))
        }
      } else {
        callback(new Error('必填'))
      }
    }
    return {
      states: [{
        value: '1',
        label: '是'
      }, {
        value: '0',
        label: '否'
      }],
      listLoading: true,
      list: [],
      upLoadUrl: `${config.baseUrl}/file/upload/image`,
      ruleForm: {
        state: '1'
      },
      fileList: [],
      rules: {
        name: [{ required: true, message: '必填', trigger: 'blur' }],
        orderId: { required: true, validator: checkOrder, trigger: 'blur' },
        imgUrl: [{ required: true, message: '必选', trigger: 'change' }]
      },
      name: '',
      type: '',
      title: '',
      dialogFormVisible: false
    }
  },
  methods: {
    delete1 (row) {
      this.$confirm('此操作将修改该文件状态, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        API.bannerdel({
          id: row.id
        }).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.init()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    sureForm () {
          if (this.type === 'edit') {
            API.updateBanner({
              id: this.ruleForm.id,
              appId: this.ruleForm.appId,
              remark: this.ruleForm.remark,
              name: this.ruleForm.name,
              imgUrl: this.ruleForm.imgUrl,
              orderId: this.ruleForm.orderId,
              state: this.ruleForm.state
            }).then(res => {
              this.$message({
                message: '类型修改成功！',
                type: 'success'
              })
              this.init()
              this.dialogFormVisible = false
            })
          } else {
            API.addBanner({
              name: this.ruleForm.name,
              remark: this.ruleForm.remark,
              imgUrl: this.ruleForm.imgUrl,
              orderId: this.ruleForm.orderId,
              appId: sessionStorage.getItem('appId'),
              state: this.ruleForm.state
            }).then(res => {
              this.$message({
                message: '类型添加成功！',
                type: 'success'
              })
              this.init()
              this.dialogFormVisible = false
            })
          }
    },
    errorUpload (err, file, fileList) {
      this.$message({
        message: err,
        type: 'warning'
      })
    },
    movefile (file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`).then(res => {
        this.ruleForm.imgUrl = ''
      })
    },
    morefile (files, fileList) {
      this.$message({
        message: '已经存在上传的文件，请上传或者删除！',
        type: 'warning'
      })
    },
    beforeUpload (file) {
      const typesList = ['png', 'jpg', 'jpeg']
      const types = typesList.indexOf(file.name.split('.')[file.name.split('.').length - 1])
      const isJPG = types === '-1'
      const isLt2M = file.size / 1024 / 1024 < 5
      if (isJPG) {
        this.$message.error('请上传正确的图片格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过5MB!')
      }
      return !isJPG && isLt2M
    },
    successUpload (response, file, fileList) {
      this.ruleForm.imgUrl = response.data.url
    },
    initBanner (id) {
      API.bannerDetail({
        id: id,
        appId: sessionStorage.getItem('appId')
      }).then(res => {
        this.ruleForm = res
        this.fileList = [{ name: '', url: res.imgUrl }]
      })
    },
    init () {
      this.listLoading = true
      API.getBannerList({
        appId: sessionStorage.getItem('appId'),
        name: this.name
      }).then(res => {
        this.listLoading = false
        this.$set(this, 'list', res.pageList);
        console.log(this.list)
      })
    },
    add () {
      this.title = '添加幻灯片'
      this.ruleForm = {
        state: '1'
      }
      this.type = 'add'
      if (this.$refs['ruleForm'] !== undefined) {
        this.$refs['ruleForm'].resetFields()
      }
      if (this.$refs['upload'] !== undefined) {
        this.$refs['upload'].clearFiles()
      }
      this.dialogFormVisible = true
    },
    edit (row) {
      this.type = 'edit'
      this.title = `${row.name}修改`
      if (this.$refs['ruleForm'] !== undefined) {
        this.$refs['ruleForm'].resetFields()
      }
      if (this.$refs['upload'] !== undefined) {
        this.$refs['upload'].clearFiles()
      }
      this.initBanner(row.id)
      this.dialogFormVisible = true
    }
  },
  mounted () {
    // console.log(this.$route)
    this.init();
  }
}
</script>

<style scoped>
.demo-ruleForm .el-form-item,.demo-ruleForm .upload-demo{
  text-align: left;
}
</style>
